html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
/* Colors */
/* Fonts */
@font-face {
  font-family: 'Neusa Medium';
  src: url("../fonts/neusa_medium-webfont.woff2") format('woff2'), url("../fonts/neusa_medium-webfont.woff") format('woff');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Neusa Semibold';
  src: url("../fonts/neusa_semibold-webfont.woff2") format('woff2'), url("../fonts/neusa_semibold-webfont.woff") format('woff');
  font-weight: normal;
  font-style: normal;
}
/* Global styles */
body {
  font-family: Neusa Medium;
  color: #4f4f4f;
  transition: all 250ms ease-in-out;
}
.translate-body {
  transform: translate(-250px, 0);
}
svg#svg {
  display: none;
}
.flex-container {
  width: 100%;
  max-width: 1080px;
  margin: auto;
  display: flex;
}
a {
  text-decoration: none;
  color: inherit;
}
.grid {
  display: flex;
  flex-direction: column;
}
@media screen and (min-width: 760px) {
  .grid {
    flex-direction: row;
  }
}
.grid .col {
  flex: 1;
}
.section {
  padding: 3.75em 0;
}
.section-big-title,
.section-subtitle,
.section-small-title,
.section-text {
  line-height: 1.3;
}
.section-big-title {
  font-family: Neusa Semibold;
  font-size: 1.375em;
}
@media screen and (min-width: 1080px) {
  .section-big-title {
    font-size: 2em;
  }
}
.section-subtitle {
  font-family: 'Raleway', sans-serif;
  color: #808080;
  font-size: 1.125em;
}
.section-subtitle b {
  font-weight: 700;
}
.section-small-title {
  color: #1878be;
  font-size: 1.25em;
}
.section-small-title b {
  font-family: Neusa Semibold;
}
.section-text {
  font-family: 'Raleway', sans-serif;
}
.line {
  width: 100%;
  height: 1px;
  border: 0;
  background-color: #fff;
}
.with-pattern {
  position: absolute;
  width: 100%;
  height: 100%;
  display: block;
  top: 0;
  left: 0;
  background-image: url("../images/back-pattern.png");
  background-repeat: repeat;
  z-index: -1;
  opacity: 0.3;
}
/* Modules */
.btn {
  padding: 0.4375em 1.375em;
  border: 1px solid transparent;
  border-radius: 1.6875em;
  display: inline-block;
}
.btn--blue {
  background-color: #1878be;
  color: #fff;
}
.btn--border {
  border-color: #fff;
}
.btn--cian {
  background-color: #699;
  color: #fff;
}
/* Layout styles */
.overlay-on-small {
  width: 100%;
  height: 100%;
  background-color: rgba(51,51,51,0.4);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 998;
  display: none;
}
.header .top-nav {
  display: flex;
  justify-content: space-between;
  padding: 1.4375em;
}
@media screen and (min-width: 960px) {
  .header .top-nav {
    width: 100%;
    height: 90px;
    padding: 0 1.4375em;
  }
}
.header .top-nav .top-nav-logo {
  display: flex;
  justify-content: center;
  align-items: center;
}
.header .top-nav .top-nav-logo img {
  width: auto;
  height: 40px;
}
.header .top-nav .top-nav-responsive {
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}
@media screen and (min-width: 960px) {
  .header .top-nav .top-nav-responsive {
    display: none;
  }
}
.header .top-nav .top-nav-responsive svg {
  width: 32px;
  height: 32px;
  fill: #1878be;
}
.header .top-nav .top-nav-menu {
  display: block;
  position: fixed;
  top: 0;
  right: 0;
  width: 250px;
  height: 100%;
  background-color: #1878be;
  transform: translate(250px, 0);
  z-index: 999;
  box-shadow: -1px 0 3px rgba(79,79,79,0.4);
}
@media screen and (min-width: 1080px) {
  .header .top-nav .top-nav-menu {
    display: block;
    position: relative;
    width: auto;
    height: auto;
    transform: translate(0, 0);
    background: none;
    box-shadow: none;
  }
  .header .top-nav .top-nav-menu ul {
    display: flex;
    height: 100%;
  }
  .header .top-nav .top-nav-menu ul li ul {
    display: flex;
    visibility: hidden;
    opacity: 0;
  }
}
.header .top-nav .top-nav-menu ul li {
  flex-direction: column;
  align-items: flex-start;
  display: flex;
  color: #fff;
}
@media screen and (min-width: 1080px) {
  .header .top-nav .top-nav-menu ul li {
    color: #4f4f4f;
  }
}
.header .top-nav .top-nav-menu ul li a {
  line-height: 50px;
  display: block;
  padding: 0 1.375em;
}
@media screen and (min-width: 1080px) {
  .header .top-nav .top-nav-menu .list-item {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
  }
  .header .top-nav .top-nav-menu .list-item:hover {
    color: #1878be;
  }
}
.header .top-nav .top-nav-menu .list-item:hover .sub-nav {
  display: flex;
}
@media screen and (min-width: 1080px) {
  .header .top-nav .top-nav-menu .list-item:hover .sub-nav {
    visibility: visible;
    opacity: 1;
  }
}
.header .top-nav .top-nav-menu .btn {
  border-color: #fff;
  line-height: 2;
  margin: auto;
  display: inline-block;
}
.header .top-nav .sub-nav {
  background-color: #6195cb;
  display: none;
  flex-direction: column;
  width: 100%;
  transition: all 150ms ease;
}
@media screen and (min-width: 1080px) {
  .header .top-nav .sub-nav {
    left: 0;
    top: 100%;
    position: absolute;
    background-color: #fff;
    width: auto;
    height: auto !important;
    border-top: 3px solid #1878be;
  }
  .header .top-nav .sub-nav li {
    position: relative;
    padding: 0;
  }
  .header .top-nav .sub-nav li a {
    display: block;
    width: 100%;
    white-space: nowrap;
  }
  .header .top-nav .sub-nav li a:hover {
    color: #1878be;
  }
}
.header .bottom-nav {
  border-top: 3px solid #1878be;
  background-color: #6195cb;
  color: #fff;
  padding: 1em 1.375em;
  font-size: 0.875em;
}
@media screen and (min-width: 960px) {
  .header .bottom-nav {
    font-size: 1em;
  }
}
.header .bottom-nav .flex-container {
  flex-direction: column;
}
@media screen and (min-width: 768px) {
  .header .bottom-nav .flex-container {
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
  }
}
@media screen and (min-width: 960px) {
  .header .bottom-nav .flex-container {
    justify-content: space-between;
  }
}
.header .bottom-nav .bottom-nav-info {
  font-family: Neusa Semibold;
  display: flex;
  justify-content: space-between;
  margin: 0.875em 0 0 0;
  letter-spacing: 0.5px;
}
@media screen and (min-width: 768px) {
  .header .bottom-nav .bottom-nav-info {
    margin: 0;
  }
  .header .bottom-nav .bottom-nav-info span:first-child {
    border-right: 2px solid #fff;
    margin-right: 1rem;
    padding-right: 1rem;
  }
}
.header .bottom-nav .bottom-nav-search {
  display: none;
}
@media screen and (min-width: 960px) {
  .header .bottom-nav .bottom-nav-search {
    display: block;
  }
  .header .bottom-nav .bottom-nav-search label {
    position: relative;
  }
  .header .bottom-nav .bottom-nav-search label input {
    outline: 0;
  }
  .header .bottom-nav .bottom-nav-search label svg {
    position: absolute;
    right: 12px;
    top: 3px;
    width: 16px;
    height: 16px;
    fill: #1878be;
  }
}
.footer {
  padding: 2.5em;
  padding-bottom: 0;
  font-family: 'Raleway', sans-serif;
}
@media screen and (min-width: 1080px) {
  .footer {
    padding-bottom: 1.25em;
  }
}
.footer .flex-container {
  flex-wrap: wrap;
}
@media screen and (max-width: 375px) {
  .footer .flex-container {
    flex-direction: column;
    padding-bottom: 2.5em;
  }
}
.footer .footer-nav-item {
  width: 50%;
  height: auto;
}
@media screen and (max-width: 375px) {
  .footer .footer-nav-item {
    width: 100%;
  }
}
@media screen and (min-width: 760px) {
  .footer .footer-nav-item {
    width: 33.333333333333336%;
  }
}
@media screen and (min-width: 1080px) {
  .footer .footer-nav-item {
    width: 20%;
  }
}
.footer .footer-nav-item .footer-nav-title {
  color: #1878be;
  margin-bottom: 1.375em;
  display: block;
}
@media screen and (min-width: 960px) {
  .footer .footer-nav-item .footer-nav-title {
    margin-bottom: 1em;
  }
}
.footer .footer-nav-item .footer-nav-title b {
  font-weight: 700;
}
.footer .footer-nav-item ul {
  margin-bottom: 1.375em;
}
.footer .footer-nav-item ul li {
  line-height: 1.5;
}
@media screen and (min-width: 960px) {
  .footer .footer-nav-item ul li {
    font-size: 14px;
  }
}
.footer .footer-nav-item .footer-nav-icons {
  display: flex;
}
.footer .footer-nav-item .footer-nav-icons li a {
  margin-right: 0.5em;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ddd;
}
.footer .footer-nav-item .footer-nav-icons li a svg {
  width: 16px;
  height: 16px;
  fill: #fff;
}
.copyright {
  background-color: #1878be;
  color: #fff;
  font-size: 0.875em;
  text-align: center;
  padding: 1.375em 0;
}
/* Pages */
.slider-wrapper {
  width: 100%;
  height: auto;
}
.slider-wrapper .slider {
  width: 100%;
  height: 350px;
  position: relative;
}
@media screen and (min-width: 760px) {
  .slider-wrapper .slider {
    height: 450px;
  }
}
.slider-wrapper .slider .slider-item {
  width: 100%;
  height: 100%;
}
.slider-wrapper .slider .slider-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  display: block;
}
.slider-wrapper .slider .slider-item .flex-container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  padding: 1.25em 2.5em;
  z-index: 500;
}
@media screen and (min-width: 760px) {
  .slider-wrapper .slider .slider-item .flex-container {
    justify-content: flex-start;
    padding: 0 0 5% 5%;
  }
}
@media screen and (min-width: 1080px) {
  .slider-wrapper .slider .slider-item .flex-container {
    padding: 0 0 5% 0;
  }
}
.slider-wrapper .slider .slider-item .flex-container .slider-item-content {
  background-color: rgba(255,255,255,0.2);
  width: 100%;
  height: auto;
  padding: 0.875em 2em;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}
.slider-wrapper .slider .slider-item .flex-container .slider-item-content .btn {
  margin-top: 0.5em;
}
@media screen and (min-width: 760px) {
  .slider-wrapper .slider .slider-item .flex-container .slider-item-content {
    max-height: 150px;
    width: 50%;
