  .slider-wrapper .slider .slider-item .flex-container .slider-item-content .section-big-title {
    font-size: 1.5em;
    max-width: 90%;
    line-height: 1.2;
  }
}
@media screen and (min-width: 960px) {
  .slider-wrapper .slider .slider-item .flex-container .slider-item-content .section-big-title {
    font-size: 1.75em;
  }
}
.slider-wrapper .slider-footer {
  background-color: #ddd;
  display: flex;
  justify-content: space-around;
  padding: 0.5em 0;
  max-width: 960px;
  position: relative;
  z-index: 9999;
}
@media screen and (max-width: 375px) {
  .slider-wrapper .slider-footer {
    flex-direction: column;
    padding-left: 1.375em;
  }
}
@media screen and (min-width: 960px) {
  .slider-wrapper .slider-footer {
    top: -1em;
    margin: 0 auto;
  }
}
.slider-wrapper .slider-footer a {
  display: flex;
  max-width: 140px;
  align-items: center;
  font-size: 14px;
  line-height: 1.3;
}
.slider-wrapper .slider-footer a svg {
  width: 40px;
  height: 40px;
  fill: #1878be;
  margin-right: 12px;
}
@media screen and (min-width: 1080px) {
  .section-affiliates .flex-container {
    padding-left: 3.75em;
    padding-right: 3.75em;
  }
}
.section-affiliates .section-big-title {
  color: #1878be;
}
.section-affiliates .section-subtitle {
  margin: 1em 0 1.75em 0;
}
.section-affiliates .section-small-title {
  margin-bottom: 0.875em;
}
.section-affiliates .grid .col {
