}
.section-plans {
  background-color: #ddd;
  width: 100%;
  height: auto;
}
@media screen and (min-width: 760px) {
  .section-plans {
    height: 350px;
  }
}
@media screen and (min-width: 960px) {
  .section-plans {
    height: 350px;
    padding: 0;
  }
}
.section-plans .section-big-title {
  color: #699;
}
.section-plans .grid {
  height: 100%;
}
.section-plans .grid .col {
